import React from "react";
import PropTypes from "prop-types";

import { Skeleton } from "antd";
import { Bar } from "react-chartjs-2";

// import data from "./chart_mock";

const AggregatePricesChart = (props) => {
  const { data } = props;

  const aggregateData = { labels: [], datasets: [] };
  aggregateData.labels = this.state.fruitList;
  aggregateData.datasets = aggregateData.datasets.concat([{
    label: "Venda acumulada",
    backgroundColor: "#975FE4",
    data: this.state.fruitList.map((fruit, index) => {
      const fruitIndex = this.state.fruitIndexList[index];
      // Predicted_price * Producao_estimada
      return this.predictionList[fruitIndex].slice(-15).reduce((sum, p, idx) => sum + p * this.dataList[fruitIndex][idx][4], 0) / 2.0;
    })
  }, {
    label: "Custo acumulado",
    backgroundColor: "#550055",
    data: this.state.fruitList.map((fruit, index) => {
      const fruitIndex = this.state.fruitIndexList[index];
      // defensivos_unidade_hc * 5
      return this.dataList[fruitIndex].slice(-15).reduce((sum, p) => sum + p[12] * 10, 0);
    })
  }]);

  if (!data || data.length === 0) {
    return <Skeleton active />;
  }
  return (
    <>
      <Bar
        data={data}
        options={{
          legend: {
            labels: {
              fontFamily: "Open Sans",
              fontSize: 14
            }
          },
          cornerRadius: 2,
          scales: {
            xAxes: [{
              maxBarThickness: 42,
              gridLines: {
                display: false
              },
              ticks: {
                fontColor: "#333",
                fontFamily: "Open Sans",
                fontSize: 14
              }
            }],
            yAxes: [{
              gridLines: {
                display: false
              },
              min: 0,
              ticks: {
                fontColor: "#333",
                fontFamily: "Open Sans",
                fontSize: 14,
                beginAtZero: true,
              }
            }]
          }
        }}
      />
    </>
  );
};

AggregatePricesChart.propTypes = {
  data: PropTypes.array,
};

export default AggregatePricesChart;
